<template>
	<Slider :delay=3000 />
	<div v-if="!loaded" class="loading">载入中，请稍等……</div>
	<div class="container" :loading="!loaded">
		<div class="title">介绍</div>
		<div ref="content" class="content"></div>
		<div class="line"></div>
		<div class="content control">
			<button @click="backHome">返回首页</button>
			<button @click="goServices">业务咨询</button>
			<button @click="onPromote">推广本页</button>
		</div>
		<div class="qrcode">
			<div class="title">本页推广码</div>
			<div ref="qrcode"></div>
		</div>
		<FootMark />
	</div>
	<QRCodeView />
</template>

<style scoped>
.loading {
	font-size: 24px;
	text-align: center;
}
.container {
	font-size: 16px;
	opacity: 1;
	transition: opacity 300ms ease-in-out;
}
.container[loading="true"] {
	opacity: 0;
}
.container .title {
	margin-top: 20px;
	margin-left: 5px;
	font-size: 24px;
	font-weight: bolder;
}
.container .content {
	padding: 0px 10px;
	line-height: 24px;
}
.container .content.control {
	padding-top: 20px;
	text-align: center;
}
.container .content.control button {
	padding: 10px 20px;
	margin: 0px 10px;
	background-color: white;
	outline: none;
	border-radius: 5px;
	border: none;
	box-shadow: inset 0px 0px 2px rgb(205, 205, 205), 1px 1px 3px rgba(75, 75, 75, 0.2);
	font-size: 14px;
	cursor: pointer;
	transition: all 300ms ease-in-out;
}
.container .content.control button:hover {
	box-shadow: inset 0px 0px 0px rgb(175, 175, 175), 2px 2px 5px rgba(36, 36, 36, 0.5);
}
.container .line {
	margin: 10px 5px;
	height: 1px;
	background-color: rgb(225, 225, 225);
}
.container .qrcode {
	margin-bottom: 10px;
}
.container .qrcode .title {
	margin-top: 20px;
	margin-bottom: 10px;
	font-size: 14px;
	text-align: center;
}
</style>

<script>
import Slider from '@/components/slider.vue';
import FootMark from '@/components/footmark.vue';
import QRCodeView from '@/components/qrcode.vue';

export default {
	name: 'Promote',
	components: {Slider, FootMark, QRCodeView},
	data () {
		return {
			loaded: false,
		}
	},
	methods: {
	},
	mounted () {
		this.loaded = false;
		var rect = document.body.getBoundingClientRect();
		var width = rect.width, height = rect.height;
		if (width > 800) width = 800;

		var {id} = this.$route.params;
		if (!id) {
			height = height / 3;
			EventMetro.pub('window_resize', width, height);
			return;
		}
		height *= 1 / 2;
		EventMetro.pub('window_resize', width, height);

		var session = localStorage.get('sessionID') || '';

		this.showInfo(id, session);
	},
	methods: {
		async showInfo (id, session) {
			var visited = sessionStorage.get('visit-promotion-' + id);
			var info = await BackEnd.getPromotion(id, visited, session);
			info.promotionHash = info.promotionHash || id;
			if (!info) {
				return;
			}
			sessionStorage.set('visit-promotion-' + id, true);

			this.$refs.content.innerHTML = info.desc || '暂无信息……';
			var slides = info.pics.map(pic => {
				return { pic };
			});
			EventMetro.pub('update_slider', slides);

			var url = location.origin + '/promote/' + info.promotionHash;
			this.$refs.qrcode.innerHTML = '';
			var qrcode = new QRCode(this.$refs.qrcode, {
				text: url,
				width: 200,
				height: 200,
				colorDark : "#000000",
				colorLight : "#ffffff",
				correctLevel : QRCode.CorrectLevel.H
			});
			var qr = this.$refs.qrcode.querySelector('img');
			qr.style.marginLeft = 'auto';
			qr.style.marginRight = 'auto';
			qr.style.maxWidth = '100%';

			this.loaded = true;
		},
		backHome () {
			var {id} = this.$route.params;
			if (!!id) {
				this.$router.push({path: '/', query: {hash: id}});
			}
			else {
				this.$router.push({path: '/'});
			}
		},
		onPromote () {
			EventMetro.pub('promote');
		},
		goServices () {
			var hash = this.$route.params.id;
			if (!!hash) this.$router.push({path: '/services', query: {hash}});
			else this.$router.push({path: '/services'});
		},
	}
}
</script>